<template>
  <div
    class="dragItem"
    :class="[dir, { hide: hide }]"
    :style="{
      width: sizeList[index].width + '%',
      height: sizeList[index].height + '%',
    }"
  >
    <div
      v-if="showTouchBar"
      class="touchBar"
      :style="{
        width: dir === 'h' ? touchBarSize + 'px' : '100%',
        height: dir === 'h' ? '100%' : touchBarSize + 'px',
      }"
      :class="[{ canDrag: !disabled }, dir]"
      @mousedown="onMousedown"
    >
      <span class="title">{{ title }}</span>
    </div>
    <slot></slot>
  </div>
</template>

<script setup>
import {
  defineProps,
  onBeforeUnmount,
  watch,
  inject,
  getCurrentInstance,
  defineEmits,
} from "vue";
import Drag from "@/utils/Drag.js";

// props
const props = defineProps({
  // 
  disabled: {
    type: Boolean,
    default: false,
  },
  // 
  touchBarSize: {
    type: Number,
    default: 20,
  },
  // `DragItem`0
  index: {
    type: Number,
    default: 0,
  },
  // 
  showTouchBar: {
    type: Boolean,
    default: true,
  },
  // 
  title: {
    type: String,
    default: "",
  },
  // 
  hide: {
    type: Boolean,
    default: false,
  },
});

// 
const emit = defineEmits(["size-change"]);

// hooks

// 
const useInit = () => {
  const dir = inject("dir");

  return {
    dir,
  };
};

// 
const useSizeList = ({ emit }) => {
  const sizeList = inject("sizeList");

  watch(
    [
      () => {
        return sizeList.value[props.index].width;
      },
      () => {
        return sizeList.value[props.index].height;
      },
    ],
    () => {
      emit("size-change");
    }
  );

  return {
    sizeList,
  };
};

// 
const useDrag = ({ props }) => {
  const { proxy } = getCurrentInstance();
  const onDragStart = inject("onDragStart");
  const onDrag = inject("onDrag");
  // 
  let drag = null;
  if (!props.disabled) {
    drag = new Drag(
      (...args) => {
        onDragStart(...args);
      },
      (...args) => {
        onDrag(props.index, ...args);
      },
      (...args) => {
        proxy.$eventEmitter.emit("dragOver", ...args);
      }
    );
  }

  // 
  const onMousedown = (e) => {
    proxy.$eventEmitter.emit("dragStart");
    drag && drag.onMousedown(e);
  };

  // 
  onBeforeUnmount(() => {
    drag && drag.off();
  });

  return {
    onMousedown,
  };
};

// created
const { dir } = useInit();
const { sizeList } = useSizeList({ emit });
const { onMousedown } = useDrag({ props });
</script>

<style scoped lang="less">
.dragItem {
  display: flex;
  background-color: var(--editor-background);

  &.hide {
    display: none;
  }

  &.v {
    flex-direction: column;
  }

  .touchBar {
    flex-grow: 0;
    flex-shrink: 0;
    background-color: var(--touch-bar-background);

    &.canDrag {
      &.v {
        cursor: row-resize;
      }

      &.h {
        cursor: col-resize;
      }
    }

    &.h {
      border-left: 1px solid var(--touch-bar-border-left-color);
      border-right: 1px solid var(--touch-bar-border-right-color);
      height: 100%;

      .title {
        margin-left: 0px;
        margin-top: 5px;
        text-align: center;
      }
    }

    &.v {
      border-top: 1px solid var(--touch-bar-border-left-color);
      border-bottom: 1px solid var(--touch-bar-border-right-color);
      width: 100%;
    }

    .title {
      display: flex;
      align-items: center;
      color: var(--editor-header-title-color);
      font-size: 12px;
      margin-left: 5px;
    }
  }
}
</style>